'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-read-only-property';


// MAIN //

/**
* Top-level namespace.
*
* @namespace ns
*/
var ns = {};

/**
* @name dlacpy
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/lapack/base/dlacpy}
*/
import dlacpy from './../../base/dlacpy';
setReadOnly( ns, 'dlacpy', dlacpy );

/**
* @name dlaswp
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/lapack/base/dlaswp}
*/
import dlaswp from './../../base/dlaswp';
setReadOnly( ns, 'dlaswp', dlaswp );

/**
* @name dpttrf
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/lapack/base/dpttrf}
*/
import dpttrf from './../../base/dpttrf';
setReadOnly( ns, 'dpttrf', dpttrf );


// EXPORTS //

export default ns;
